.container {
  width: 30vw;
  margin-top: 30px;
  margin-bottom: 30px;
  align-items: center;
  display: flex;
  flex-direction: column;

  .seeprBTN{
    text-decoration: none;
  }


  .image {
    width: 17vw;
    height: 17vw;
    transition: transform .2s;

  }

  .nameDiv {
    display: flex;
    flex-direction: row;
    align-items: center;

    .nametxt {
      padding: 10px;
    }

  }

}

.container:hover {
  .image {
    transform: scale(1.1);

  }

  //.image[data-src] {
  //  opacity: 0;
  //}
}




@media screen and (max-width: 1200px) {
  .container {
    .nameDiv {
      .nametxt {
        padding: 5px;
        font-size: 3vw;
      }

      .nameicon{
        font-size: 3vw;
      }
    }

    .seeprBTN{
      text-decoration: none;
      font-size: 2vw;
    }

  }
}
